main {
  position: relative; }

.unselectable {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently */ }

.container {
  padding: 0px 5%;
  width: 100%; }

.absolute_center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.sub_title {
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 400; }
  .sub_title_0 {
    font-size: 12px; }
  .sub_title_1 {
    font-size: 14px; }

.middle_title {
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 500; }
  .middle_title_0 {
    font-size: 12px; }
  .middle_title_1 {
    font-size: 14px; }

.standart_title {
  font-weight: 600;
  font-stretch: normal;
  font-style: normal; }
  .standart_title_0 {
    font-size: 12px; }
  .standart_title_1 {
    font-size: 14px; }
  .standart_title_2 {
    font-size: 16px; }
  .standart_title_3 {
    font-size: 18px; }

.bold_title {
  font-weight: 700;
  font-stretch: normal;
  font-style: normal; }
  .bold_title_0 {
    font-size: 12px; }

.color_red {
  color: #BC1600; }

.color_black {
  color: #15191B; }
  .color_black_2 {
    color: #212129; }

.color_white {
  color: #fff; }

.color_gray {
  color: #686874; }
