#contact {
  background-color: #777;
  padding: 15px 0 30px;
  font-size: .85rem; }

#contact ul {
  list-style: none; }

#contact ul li a {
  color: #ccc; }

#contact ul li a:hover {
  color: #fff; }

#contact .row {
  display: flex;
  flex-wrap: wrap;
  color: #ccc;
  margin: auto;
  justify-content: space-between; }

#contact .row .c25 {
  flex-basis: 25%; }

#contact .row .c25:not(:first-child) {
  /* padding-left: 15px; */ }

#contact h3 {
  color: #fff;
  font-size: 1rem;
  letter-spacing: .01em;
  font-weight: 700;
  margin-top: 1.5em;
  margin-bottom: .5em; }

#contact ul,
#contact p {
  line-height: 1.8; }

#contact .poshta {
  display: inline-block;
  max-width: 170px;
  background-color: #dedede;
  padding: 7px 15px 7px 12px;
  margin-top: .4em;
  margin-bottom: .5em; }

#contact .social-icons {
  margin: .75em 0; }

.icon-txt {
  display: flex;
  align-items: center;
  margin-bottom: .4em;
  margin-left: .2em; }

.icon-txt :first-child {
  width: 20px; }

#contact .material-icons {
  color: #ccc;
  opacity: 1;
  font-size: 1.25em; }

.fa-envelope {
  font-size: 1.2em; }

.fa-mobile-alt {
  font-size: 1.55em;
  padding-left: .1em; }

.fa-phone-alt {
  font-size: 1.1em;
  padding-left: .1em; }

.fab,
.share .fas {
  color: #ccc;
  font-size: 1.7em;
  margin-right: 12px; }

a:hover .fab {
  color: #fff; }

.share {
  display: flex;
  align-items: center;
  margin: 2.5em 0; }

.share span {
  color: #aaa;
  font-size: .9rem;
  font-style: italic;
  font-weight: 600;
  margin-right: 1em; }

.share a:hover .fab,
.share a:hover .fas {
  color: #900; }

@media screen and (max-width: 1010px) {
  #contact .poshta {
    width: auto;
    height: 65px; }
  #contact .poshta:first-of-type {
    padding-top: 12px;
    margin-right: 10px; }
  #top #contact .row .c25 {
    flex-basis: 48%;
    padding-left: 0; } }

@media screen and (max-width: 520px) {
  #contact ul li {
    display: inline-block;
    margin-right: .5em; }
  #top #contact .row .c25 {
    flex-basis: 100%; } }
