.swiper-wrapper .item {
  height: 100%; }

.swiper-slide, .item {
  height: auto; }

.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  display: inline-block;
  margin: 0 7px;
  cursor: pointer; }

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background-color: #fff;
  opacity: .4; }

.swiper-pagination-bullet-active {
  background-color: #fff;
  opacity: 1; }

.swiper-button-next, .swiper-button-prev {
  color: #aaa;
  opacity: 1; }

header .swiper-button-next, header .swiper-button-prev {
  color: #fff;
  opacity: .6; }

.swiper-button-next:hover, .swiper-button-prev:hover {
  color: #900;
  opacity: 1; }

header .swiper-button-next:hover, header .swiper-button-prev:hover {
  color: #fff;
  opacity: 1; }

.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: 2.5rem; }

.swiper-button-prev {
  left: 50%;
  margin-left: -645px; }

.swiper-button-next {
  right: 50%;
  margin-right: -645px; }

@media screen and (min-width: 769px) {
  .slider .swiper-button-next, .slider .swiper-button-prev {
    margin-top: -47px; }
  #kits .swiper-button-next, #kits .swiper-button-prev {
    margin-top: -47px; }
  #blog .swiper-button-next, #blog .swiper-button-prev {
    margin-top: -36px; } }

@media screen and (max-width: 1300px) {
  .swiper-button-next:after,
  .swiper-button-prev:after {
    font-size: 1.5rem;
    font-weight: bold; }
  .swiper-button-next {
    right: 0;
    margin-right: 5px; }
  .swiper-button-prev {
    left: 0;
    margin-left: 5px; } }

@media screen and (max-width: 768px) {
  .swiper-button-prev,
  .swiper-button-next {
    top: 15px;
    margin-right: 15px;
    margin-left: 15px; }
  header .swiper-button-prev,
  header .swiper-button-next {
    bottom: 0;
    top: auto; } }
