@charset "UTF-8";
section .swiper-container {
  padding-top: 60px;
  max-width: 1300px;
  padding-left: 50px;
  padding-right: 50px; }

section .swiper-container:before {
  background-color: #f0efed;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  left: -1px;
  top: 0;
  width: 43px;
  z-index: 2; }

section .swiper-container:after {
  background-color: #f0efed;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  right: -1px;
  top: 0;
  width: 43px;
  z-index: 2; }

section .swiper-wrapper {
  height: auto; }

#blog .swiper-container {
  max-width: 1300px;
  padding-left: 50px;
  padding-right: 50px; }

section .swiper-container .title {
  color: #888;
  letter-spacing: .1em;
  font-size: 1.5em;
  position: absolute;
  width: 100%;
  left: 0;
  top: 0; }

section .swiper-container .title a {
  color: #888; }

section .swiper-container .title a:hover {
  color: #900; }

.book-cover {
  position: relative;
  overflow: visible;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start; }

.book-cover > a {
  position: relative; }

.blog-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 0; }

.book-cover .txt-box {
  margin-top: 1em; }

.blog-item .txt-box {
  margin-top: .5em; }

.book-cover img,
.blog-item img {
  /* max-width:100%; */
  filter: drop-shadow(2px 2px 3px #888); }

.book-cover.wide img {
  /* max-width: none; */ }

.blog-item img {
  max-width: 100%; }

.blog-item > a:first-child {
  position: relative; }

.book-cover span {
  max-width: 100%; }

.to-buy {
  background-color: inherit;
  position: absolute;
  left: 0;
  bottom: 0;
  transform: translateY(100%);
  transition: all .2s ease;
  width: 100%;
  opacity: 0; }

.to-buy .material-icons {
  font-size: 2.35em; }

.book-cover a img,
.blog-item a img {
  transition: transform .35s ease; }

.book-cover a:hover img,
.blog-item a:hover img {
  transform: scale(1.02); }

.to-buy:hover:after {
  font-size: 1.25em;
  display: inline-block;
  content: "купити";
  position: absolute;
  left: 50px;
  top: 10px; }

.book-cover .price {
  background-color: #fff;
  color: #900;
  font-size: .85em;
  font-weight: 600;
  padding: 4px;
  position: absolute;
  right: 0;
  top: 0;
  margin-right: 40px;
  text-align: center;
  z-index: -1;
  transition: transform .3s ease;
  filter: drop-shadow(4px 4px 4px #aaa); }

.book-cover:hover .price {
  transform: translateY(-20px); }

.stick {
  background-image: url("/static/source/img/img/stick_a.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  position: absolute;
  right: -8px;
  top: -18px;
  width: 56px;
  height: 56px;
  color: #800;
  font-size: .75em;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: .05em;
  text-align: center;
  padding-top: 21px;
  /* transform: rotate(-30deg); */
  filter: drop-shadow(0px 0px 4px #777); }

.category {
  display: block;
  color: #000;
  font-size: 1em;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .05em;
  margin-top: 1.25em; }

:hover > .category {
  color: #900; }

.book-title {
  font-size: .9em;
  font-weight: 400; }

.book-author {
  font-size: .8em;
  font-weight: 600; }

@media screen and (max-width: 768px) {
  #blog .swiper-container,
  section .swiper-container {
    padding-left: 20px;
    padding-right: 20px; }
  section .swiper-container:before {
    width: 18px; }
  section .swiper-container:after {
    width: 13px; } }

@media screen and (max-width: 480px) {
  section .swiper-container .title {
    font-size: 1.35em; }
  section .swiper-container {
    padding-top: 60px; }
  .book-title {
    font-size: .8em; }
  .book-author {
    font-size: .75em; } }
