header {
  background-color: #bbb; }

header .container-fluid {
  position: relative;
  padding: 0; }

header h2 {
  font-size: 1.9em;
  font-weight: 800; }

header p {
  font-size: 1.2em; }

.video-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden; }

.video-wrapper video {
  margin: auto;
  height: 100%;
  width: 100%;
  opacity: .2;
  object-fit: cover; }

.video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #bbb;
  mix-blend-mode: hue;
  opacity: 1;
  z-index: 0; }

.swiper-slide .container {
  padding-top: 40px;
  padding-bottom: 50px;
  position: relative;
  z-index: 2; }

.cover {
  font-size: 1.1em;
  margin-top: 30px;
  max-width: 450px;
  min-width: 250px;
  width: 100%;
  position: relative; }

header .txt-box {
  font-size: 1.25em;
  font-weight: 400;
  line-height: 1.2;
  max-width: 550px;
  width: 100%; }

header .txt-box p:last-child {
  margin-bottom: 0; }

.cover .price {
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  right: 10px;
  top: 0;
  width: 82px;
  height: 82px;
  margin-top: -55px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  padding-top: 12px;
  z-index: -1; }

.new-price {
  font-size: 1.15em;
  line-height: 1;
  font-weight: 700;
  color: #900; }

.old-price {
  font-size: 1em;
  line-height: 1;
  color: #000;
  text-decoration: line-through; }

.currency {
  font-size: .8em; }

.navbar_search_input {
  transition: all .2s;
  right: -100px;
  display: block;
  padding: 5px;
  font-size: 14px;
  z-index: -1;
  overflow: hidden;
  opacity: 0;
  width: 0px;
  border-bottom: .0625em solid #888; }

.navbar_search__block {
  cursor: pointer;
  display: flex;
  align-items: center; }
  .navbar_search__block:hover .navbar_search_input {
    right: 0;
    z-index: 999;
    width: 220px;
    overflow: visible;
    opacity: 1; }

@media (max-width: 630px) {
  .navbar_search_input {
    position: absolute;
    left: -30px;
    top: -5px; }
  .navbar_search__block {
    position: relative;
    transition: all .2s;
    top: 0;
    right: 0; }
    .navbar_search__block:hover .navbar_search_input {
      left: 35px;
      width: 160px; } }

@media screen and (max-width: 1020px) {
  header {
    margin-top: 70px; } }

@media screen and (max-width: 992px) {
  header {
    margin-top: 70px; }
  header .txt-box {
    font-size: 1em; }
  .cover {
    max-width: unset; } }

@media screen and (orientation: portrait) and (max-width: 767px) {
  header h2 {
    font-size: 1.5em; } }

@media screen and (orientation: portrait) and (max-width: 620px) {
  header h2 {
    font-size: 1.25em; }
  header p {
    font-size: 1em; }
  .cover {
    max-width: 260px;
    margin-bottom: 20px; }
  #book .cover {
    max-width: unset; }
  .swiper-slide .row {
    flex-wrap: wrap;
    align-items: flex-start;
    flex-direction: column; }
  .swiper-slide .row .col {
    margin-left: 0; } }
